.pc-settings-quickActions-card {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 0.5em;
    padding: 0.5em;
    margin-bottom: 1em;
}

@media (width <=1040px) {
    .pc-settings-quickActions-card {
        grid-template-columns: repeat(2, 1fr);
    }
}

.pc-settings-quickActions-pill {
    all: unset;
    background: var(--redesign-button-secondary-background);
    color: var(--text-default);
    display: flex;
    align-items: center;
    gap: 0.5em;
    padding: 8px 12px 8px 8px;
    border-radius: 8px;
    transition: 0.1s ease-out;
    box-sizing: border-box;
    line-height: 1;
    cursor: pointer;
}

.pc-settings-quickActions-pill:hover {
    background: var(--redesign-button-secondary-pressed-background);
    transform: translateY(-1px);
    box-shadow: var(--elevation-high);
}

.pc-settings-quickActions-pill:focus-visible {
    outline: 2px solid var(--border-focus);
    outline-offset: 2px;
}

.pc-settings-quickActions-img {
    width: 24px;
    height: 24px;
    flex-shrink: 0;
}